const prefixer = code => '____sharedrop__' + code;
function setData(code, data) {
    let allData = localStorage.getItem(prefixer(''));
    if(allData) {
        allData = JSON.parse(allData);
        allData.push({code, response: data})
    } else {
        allData = [{ code, response: data }]

    }

    localStorage.setItem(prefixer(''), JSON.stringify(allData));


}

function getData() {
    let allData = localStorage.getItem(prefixer(''));

    if (allData) { 
        return JSON.parse(allData).reverse();
    }

}
function clear() {
    localStorage.setItem(prefixer(''), '[]');
}

module.exports.util = {setData,  getData, clear}